@import "../vars";
.m-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all ease-out .225s;
    padding: 0;
    &:focus {
        box-shadow: none;
    }
}

.m-btn-primary{
    height: 5em;
    text-transform: uppercase;
    background-color: $primary;
    color: $black;
    font-weight: 600;
    span{
        font-size: 1em;
        line-height: 140%;
        letter-spacing: 0.1em;
    }
    &:hover{
        background-color: $primaryHover;
        color: $black;
    }
}
